.perktree-container {
  margin-top: 5%; }
  .perktree-container .perk {
    list-style: none;
    display: flex; }
    .perktree-container .perk .title {
      position: relative; }
      .perktree-container .perk .title ::first-letter {
        font-size: 55px; }
      .perktree-container .perk .title span {
        position: absolute;
        bottom: 0;
        left: 5%; }
    .perktree-container .perk .perk-level {
      margin: 10px;
      width: 20%;
      opacity: 1; }
      .perktree-container .perk .perk-level .perk-image {
        height: 100px;
        width: 100px; }
      .perktree-container .perk .perk-level .grayscale {
        opacity: 0.8;
        filter: grayscale(66%); }
    .perktree-container .perk .perk-level .grayscale:hover {
      opacity: 1;
      filter: grayscale(0);
      cursor: pointer; }
